'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';
import { toast } from 'sonner';
import { Search, Printer, Eye, CalendarDays, ShoppingBag, User } from 'lucide-react';

// UI Components
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import {
    Table, TableBody, TableCell, TableHead, TableHeader, TableRow,
} from '@/components/ui/table';
import {
    Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger,
} from '@/components/ui/dialog';

// Tipe Data
interface DetailTransaksi {
    id: number;
    produk_resto: { nama_produk: string };
    jumlah: number;
    harga_saat_transaksi: number;
    subtotal: number;
}

interface Transaksi {
    id: number;
    kode_transaksi: string;
    nama_pelanggan: string | null;
    total_bayar: number;
    total_harga: number;
    kembalian: number;
    status: 'selesai' | 'dibatalkan';
    created_at: string;
    payment_method: { nama_metode: string };
    user: { name: string };
    details: DetailTransaksi[];
}

export default function HistoryPage() {
    const [transaksis, setTransaksis] = useState<Transaksi[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedStruk, setSelectedStruk] = useState<Transaksi | null>(null);

    // 1. Fetch Data
    const fetchHistory = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/transaksiRestos');
            setTransaksis(response.data.data); // Sesuaikan jika pakai pagination laravel (.data.data) atau (.data)
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat riwayat.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchHistory(); }, []);

    // Helper Format
    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    const formatDate = (dateString: string) => 
        format(new Date(dateString), 'dd MMMM yyyy, HH:mm', { locale: localeId });

    // Filter Logic
    const filteredData = transaksis.filter(item => 
        item.kode_transaksi.toLowerCase().includes(searchQuery.toLowerCase()) ||
        (item.nama_pelanggan && item.nama_pelanggan.toLowerCase().includes(searchQuery.toLowerCase()))
    );

    // Fungsi Print Struk (Browser Native Print)
    const handlePrint = () => {
        window.print();
    };

    return (
        <div className="space-y-6 pb-20">
            {/* Header */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 print:hidden">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Riwayat Transaksi</h1>
                    <p className="text-gray-500 text-sm">Daftar penjualan hari ini dan sebelumnya.</p>
                </div>
                <div className="relative w-full sm:w-64">
                    <Search className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                    <Input 
                        placeholder="Cari Kode / Nama Pelanggan..." 
                        className="pl-9"
                        value={searchQuery}
                        onChange={e => setSearchQuery(e.target.value)}
                    />
                </div>
            </div>

            {/* Tabel Riwayat */}
            <Card className="print:hidden border shadow-sm">
                <CardContent className="p-0">
                    <Table>
                        <TableHeader>
                            <TableRow className="bg-gray-50">
                                <TableHead>Waktu</TableHead>
                                <TableHead>Kode Transaksi</TableHead>
                                <TableHead>Pelanggan</TableHead>
                                <TableHead>Total</TableHead>
                                <TableHead>Status</TableHead>
                                <TableHead className="text-right">Aksi</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {loading ? (
                                <TableRow><TableCell colSpan={6} className="h-24 text-center">Memuat...</TableCell></TableRow>
                            ) : filteredData.length === 0 ? (
                                <TableRow><TableCell colSpan={6} className="h-32 text-center text-gray-500">Belum ada transaksi.</TableCell></TableRow>
                            ) : (
                                filteredData.map((item) => (
                                    <TableRow key={item.id} className="hover:bg-gray-50">
                                        <TableCell className="font-medium text-gray-600 text-xs">
                                            {formatDate(item.created_at)}
                                        </TableCell>
                                        <TableCell>
                                            <span className="font-mono font-bold text-gray-800">{item.kode_transaksi}</span>
                                        </TableCell>
                                        <TableCell>{item.nama_pelanggan || 'Umum'}</TableCell>
                                        <TableCell className="font-bold text-gray-900">
                                            {formatRupiah(item.total_harga)}
                                        </TableCell>
                                        <TableCell>
                                            {item.status === 'selesai' ? (
                                                <Badge className="bg-green-100 text-green-800 hover:bg-green-100 border-green-200 shadow-none">Selesai</Badge>
                                            ) : (
                                                <Badge variant="destructive">Dibatalkan</Badge>
                                            )}
                                        </TableCell>
                                        <TableCell className="text-right">
                                            <Dialog>
                                                <DialogTrigger asChild>
                                                    <Button 
                                                        variant="outline" 
                                                        size="sm" 
                                                        onClick={() => setSelectedStruk(item)}
                                                        className="hover:bg-blue-50 hover:text-blue-600"
                                                    >
                                                        <Eye className="h-4 w-4 mr-2" /> Detail
                                                    </Button>
                                                </DialogTrigger>
                                                
                                                {/* MODAL STRUK (Bisa Diprint) */}
                                                {selectedStruk && (
                                                    <DialogContent className="sm:max-w-sm bg-white p-0 overflow-hidden">
                                                        {/* --- PERBAIKAN: Tambahkan DialogHeader & Title Hidden --- */}
                                                        <DialogHeader className="sr-only">
                                                            <DialogTitle>Struk Transaksi {selectedStruk.kode_transaksi}</DialogTitle>
                                                        </DialogHeader>
                                                        {/* ------------------------------------------------------- */}

                                                        <div id="struk-area" className="p-6 bg-white">
                                                            {/* Header Struk */}
                                                            <div className="text-center border-b border-dashed pb-4 mb-4">
                                                                <h2 className="text-xl font-bold uppercase tracking-wider">MainDiHatta</h2>
                                                                <p className="text-xs text-gray-500">Jl. Olahraga No. 1, Kota Anda</p>
                                                                <p className="text-xs text-gray-500 mt-1">{formatDate(selectedStruk.created_at)}</p>
                                                            </div>

                                                            {/* Info Transaksi */}
                                                            <div className="text-xs text-gray-600 space-y-1 mb-4">
                                                                <div className="flex justify-between">
                                                                    <span>Kode:</span>
                                                                    <span className="font-mono">{selectedStruk.kode_transaksi}</span>
                                                                </div>
                                                                <div className="flex justify-between">
                                                                    <span>Kasir:</span>
                                                                    <span>{selectedStruk.user?.name}</span>
                                                                </div>
                                                                <div className="flex justify-between">
                                                                    <span>Pelanggan:</span>
                                                                    <span>{selectedStruk.nama_pelanggan || 'Umum'}</span>
                                                                </div>
                                                            </div>

                                                            {/* List Item */}
                                                            <div className="border-t border-b border-dashed py-2 mb-4 space-y-2">
                                                                {selectedStruk.details?.map((detail, idx) => (
                                                                    <div key={idx} className="text-sm">
                                                                        <div className="font-medium text-gray-900">{detail.produk_resto?.nama_produk}</div>
                                                                        <div className="flex justify-between text-gray-500 text-xs">
                                                                            <span>{detail.jumlah} x {formatRupiah(detail.harga_saat_transaksi)}</span>
                                                                            <span>{formatRupiah(detail.subtotal)}</span>
                                                                        </div>
                                                                    </div>
                                                                ))}
                                                            </div>

                                                            {/* Total */}
                                                            <div className="space-y-1 text-sm">
                                                                <div className="flex justify-between font-bold text-lg text-gray-900">
                                                                    <span>TOTAL</span>
                                                                    <span>{formatRupiah(selectedStruk.total_harga)}</span>
                                                                </div>
                                                                <div className="flex justify-between text-gray-600">
                                                                    <span>Bayar ({selectedStruk.payment_method?.nama_metode})</span>
                                                                    <span>{formatRupiah(selectedStruk.total_bayar)}</span>
                                                                </div>
                                                                <div className="flex justify-between text-gray-600">
                                                                    <span>Kembalian</span>
                                                                    <span>{formatRupiah(selectedStruk.kembalian)}</span>
                                                                </div>
                                                            </div>

                                                            {/* Footer */}
                                                            <div className="text-center mt-6 pt-4 border-t border-dashed">
                                                                <p className="text-xs text-gray-400">Terima kasih atas kunjungan Anda!</p>
                                                            </div>
                                                        </div>

                                                        {/* Tombol Print (Di luar area print) */}
                                                        <div className="p-4 bg-gray-50 border-t flex justify-center print:hidden">
                                                            <Button className="w-full bg-gray-900" onClick={handlePrint}>
                                                                <Printer className="w-4 h-4 mr-2" /> Cetak Struk
                                                            </Button>
                                                        </div>
                                                    </DialogContent>
                                                )}
                                            </Dialog>
                                        </TableCell>
                                    </TableRow>
                                ))
                            )}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>

            {/* CSS Khusus Print (Agar hanya struk yang tercetak) */}
            <style jsx global>{`
                @media print {
                    body * {
                        visibility: hidden;
                    }
                    #struk-area, #struk-area * {
                        visibility: visible;
                    }
                    #struk-area {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        padding: 0;
                        margin: 0;
                    }
                    /* Hilangkan margin halaman browser */
                    @page {
                        margin: 0;
                        size: auto;
                    }
                }
            `}</style>
        </div>
    );
}